<template>
  <el-dialog
    :visible.sync="dialogAddTask"
    width="70%"
    @close="closeDialog"
  >
 <el-input style="width:200px;" placeholder="请输入场所名称或编码"></el-input><el-button type="success" @click="search">查询</el-button>
  <div class="dialog-main">
    <p class="dialog-left"></p>
    <p class="dialog-right">
      <el-tabs v-model="activeName" @tab-click="handleClick">
        <el-tab-pane label="基本信息" name="first">
          <el-form ref="form" :model="form" label-width="80px">
            <el-form-item label="任务名称:">
              <el-input v-model="form.name" placeholder="请输入布控任务名称"></el-input>
            </el-form-item>
            <el-form-item label="布控目标:">
              <el-col :span="4">
                <el-button type="primary" size="small" @click="addTask">+</el-button>
              </el-col>
              <el-col :span="20">
                <el-select v-model="form.targetType" >
                  <el-option label="区域一" value="shanghai"></el-option>
                  <el-option label="区域二" value="beijing"></el-option>
                </el-select>
              </el-col>
            </el-form-item>
            <el-form-item label="" v-for="(item,index) in form.addTask">
              <el-col :span="20">
                <el-input style="padding:0 2px;" v-model="item.inputTarget"></el-input>
              </el-col>
              <el-col :span="4" style="text-align: center"><el-button type="primary" size="small"  @click="delTask(index)">-</el-button></el-col>
            </el-form-item>
            <el-form-item label="布控时间:">
              <el-col :span="11">
                <el-date-picker type="date" placeholder="选择日期" v-model="form.startTime" style="width: 100%;"></el-date-picker>
              </el-col>
              <el-col class="line" :span="2">-</el-col>
              <el-col :span="11">
                <el-date-picker type="date" placeholder="选择时间" v-model="form.endTime" style="width: 100%;"></el-date-picker>
              </el-col>
            </el-form-item>
          </el-form>
        </el-tab-pane>
        <el-tab-pane label="区域信息" name="second">
          <el-button type="primary">+</el-button>
          <el-tabs class="area-top" v-model="activeName2" type="card" @tab-click="handleClick" :tab-position="tabPosition">
            <el-tab-pane :label="item.label" :name="item.name" v-for="item in areaList" >
              <el-form ref="form" :model="form1" label-width="100px" class="area-main">
                <el-form-item label="时间段：">
                <el-col :span="8">
                  <el-select v-model="form1.region" placeholder="请选择活动区域">
                  <el-option label="区域一" value="shanghai"></el-option>
                  <el-option label="区域二" value="beijing"></el-option>
                  </el-select>
                </el-col>
                <el-col :span="7"><el-input></el-input></el-col>
                  <el-col :span="2">至</el-col>
                  <el-col :span="7"><el-input></el-input></el-col>
                </el-form-item>
                  <el-form-item label="出现次数：">
                    大于<el-input v-model="form1.name" style="width:80px;"></el-input>次
                  </el-form-item>
                  <el-form-item label="场所范围：">
                    <el-input type="textarea" v-model="form.desc"></el-input>
                  </el-form-item>
              </el-form>
            </el-tab-pane>
          </el-tabs>
        </el-tab-pane>
      </el-tabs>
    </p>
  </div>
  </el-dialog>
</template>

  <script>
    import {Arrangement} from '../../api/setupHttp'
    export default {
      data() {
        return {
          tabPosition: 'left',
          dialogAddTask: false,
          activeName: 'second',
          activeName2:'first',
          areaList:[{label:'区域一',name:'first'}],
          form: {
            name: '',
            startTime:'',
            endTime:'',
            targetType:'',
            addTask:[{inputTarget:''}],
          },
          form1: {
            name: '',
            region: '',
            date1: '',
            date2: '',
            delivery: false,
            type: [],
            resource: '',
            desc: ''
          }
        };
      },
      props:["outer"],
      methods: {
        closeDialog() {
          this.dialogAddTask=false;
          this.$emit("choseDialog",false)
        },
        addTask(){
          this.form.addTask.push({targetType:''})
        },
        delTask(index){
            this.form.addTask.splice(index,index+1);
        },
        handleClick(tab, event) {
        },
        search(){
        }
      },
      watch:{
        outer(newValue){
          this.dialogAddTask=newValue;
        }
      },
    };
  </script>
<style lang="scss" scoped>
  .dialog-main{
    display:flex;
    display:-webkit-flex;
    .dialog-right{
      width:500px;
      .line{
        text-align: center;
      }
      .area-top{
        padding-top:20px;
      }
      .area-main{
        background: #eee;
        padding:10px;
      }
    }
    .dialog-left{
      min-height:350px;
      flex:1;
    }
  }

</style>



