<template>
  <div class="pg2-list">
    <span class="child close" @click="hiddenList"> X </span>
    <span class="child show-list" @click="dialogTableVisible = true">详情</span>
    <ol  class="ol">
      <li class="caption">MAC地址</li>
      <li v-for="item in macList" @click="showTabList(item.code)">{{item.code}}</li>
    </ol>


    <!--  el-dialog提示信息   -->
    <el-dialog  :visible.sync="dialogTableVisible">
      <p class="dia-operation"><em @click="changeKey">列为重点</em><em>导出</em></p>
      <el-table :data="gridData">
        <el-table-column property="type" label="手机型号" width="120"></el-table-column>
        <el-table-column property="startTime" label="首次出现时间" width="150"></el-table-column>
        <el-table-column property="startPlace" label="首次出现场所" width="150"></el-table-column>
        <el-table-column property="lastTime" label="最近出现时间" width="150"></el-table-column>
        <el-table-column property="lastPlace" label="最近出现场所" width="150"></el-table-column>
        <el-table-column property="count" label="出现次数" width="150"></el-table-column>
        <el-table-column   label="" width="80">
          <template slot-scope="scope">
            <el-checkbox v-model="scope.row.checked"></el-checkbox>
          </template>
        </el-table-column>
      </el-table>
      <pageCtrl :totalCount="total" :curr="currentPage" @changePage="changePage"></pageCtrl>
    </el-dialog>
  </div>
</template>
<script>
  import pageCtrl from '../../components/pagingctrl.vue'
  export default{
    data(){
      return {
        total:30,
        currentPage:2,
        gridData:[{id:0,type:'小米C120',startTime:'2013-01-07 02:02',startPlace:'地铁梨园站',lastTime:'2017-12-22 12:02',lastPlace:'人民公园',count:10,checked:false},
          {id:1,type:'小米C120',startTime:'2013-01-07 02:02',startPlace:'地铁梨园站',lastTime:'2017-12-22 12:02',lastPlace:'人民公园',count:10,checked:false}],
        dialogTableVisible: false,
        macList:[{code:'EC-56-A8-36-23-95'},{code:'EC-56-A8-36-23-95'},{code:'EC-56-A8-36-23-95'},{code:'EC-56-A8-36-23-95'}]
      }
    },
    methods:{
      changePage(val){
      console.log("当前点击页数",val)
    },
      showTabList(code){
        console.log('emit')
        this.$emit("showTabList",code);
      },
      hiddenList(){
        this.$emit("hiddenList",false)
      },
      changeKey(){
        //    列为重点
        let arr=[];
        for(let item of this.gridData){
          if(item.checked==true){
            arr.push(item.id)
          }
        }
        return arr;
      }
    },
    components:{
      pageCtrl,
    }
  }
</script>
<style lang="scss" scoped>
.pg2-list{
  position: absolute;
  bottom:30px;
  right:0;
  width:200px;
  min-height:300px;
  background: #eee;
  .ol li{
    border-bottom:1px solid #ccc;
    height:40px;
    line-height: 40px;
    text-align: center;
    cursor: pointer;
  }
  .caption{
    background: dodgerblue;
  }
  .ol li:not(:first-child):hover{
    color:dodgerblue;
  }
  .child{
    position: absolute;
    display: inline-block;
    width: 20px;
    text-align:center;
    cursor: pointer;
  }
  .show-list{
    color:#fff;
    padding:10px 2px;
    left:-24px;
    top:120px;
    background: #409EFF;
    border-radius: 2px;
  }
  .close{
    top:5px;
    right:5px;
    color:#eee;
  }
  .close:hover{
    animation: myfirst 0.5s;
    -moz-animation: myfirst 0.55s;	/* Firefox */
    -webkit-animation: myfirst 0.5s;	/* Safari 和 Chrome */
    -o-animation: myfirst 0.5s;
    @keyframes myfirst
    {
      0% {
        opacity: 1;
      }
      50%{
        transform:rotate(45deg);
        -webkit-transform:rotate(45deg); /* Safari 和 Chrome */
        opacity:0.3;
      }
      100% {
        transform:rotate(90deg);
        -webkit-transform:rotate(90deg); /* Safari 和 Chrome */
        opacity:1;
      }
    }

  }
  .dia-operation{
    height:40px;
    text-align:right;
    em{ cursor: pointer;}
    em:hover{opacity:0.5;}
     em:last-child{
      padding:0 10px;
    }
  }
}
</style>




