<template>

  <div class="page2-wrap">
    <div class="page2-search">
      <section class="pg2-left">
        <el-input class="pg2-input" v-model="inputContext" placeholder="请输入查询内容"></el-input>
        <el-select v-model="value" placeholder="请选择">
          <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
      </section>
      <section class="pg2-right">
        <el-button type="success">查询</el-button>
      </section>
    </div>
    <div class="pg2-main">
      <div class="pg2-edit">
        <span>添加任务</span>
        <span>删除</span>
      </div>
      <table>
        <thead>
        <tr>
          <th>序号</th>
          <th>任务名称</th>
          <th>任务目标</th>
          <th>任务开始时间</th>
          <th>任务结束时间</th>
          <th>触发预警MAC</th>
          <th>任务状态</th>
          <th>选择</th>
        </tr>
        </thead>
        <tbody v-for="(item,index) in listData">
        <tr  :class="{'isOdd':index%2 != 0}">
          <td>{{index+1}}</td>
          <td :title="item.target" @click="outerVisible = true">{{item.name}}</td>
          <td :title="item.target">{{item.target}}</td>
          <td>{{item.startTime}}</td>
          <td>{{item.endTime}}</td>
          <td :title="item.target">{{item.warning}}</td>
          <td>{{item.state}}</td>
          <td>
            <el-checkbox v-model="item.checked"></el-checkbox>
          </td>
        </tr>
        </tbody>
      </table>
    </div>
    <pageCtrl :totalCount="total" :curr="currentPage" @changePage="changePage"></pageCtrl>

    <!--  dialog  部分-->
    <el-dialog   :visible.sync="outerVisible" class="dialog">
      <el-dialog
        width="30%"
        :visible.sync="innerVisible"
        append-to-body>
        <el-tabs v-model="activeName" @tab-click="handleClick">
          <el-tab-pane label="MAC信息" name="first">MAC信息</el-tab-pane>
          <el-tab-pane label="上网记录" name="second">上网记录</el-tab-pane>
          <el-tab-pane label="查看轨迹" name="third">查看轨迹</el-tab-pane>
          <el-tab-pane label="APP应用" name="fourth">APP应用</el-tab-pane>
          <el-tab-pane label="查看视频" name="five">查看视频</el-tab-pane>
        </el-tabs>
      </el-dialog>
      <div class="dialog-left">
        <table class="table table1">
          <tr>
            <td>任务名称：</td>
            <td>{{}}</td>
          </tr>
          <tr>
            <td>布控时间：</td>
            <td{{}}</td>
          </tr>
          <tr>
            <td>布控目标：</td>
            <td>{{}}</td>
          </tr>
        </table>
        <table class="table table2">
          <tr>
            <td>布控条件一</td>
            <td></td>
          </tr>
          <tr>
            <td>出现区域：</td>
            <td>{{}}</td>
          </tr>
          <tr>
            <td>出现时间：</td>
            <td{{}}</td>
          </tr>
          <tr>
            <td>出现次数：</td>
            <td>{{}}</td>
          </tr>
        </table>
        <table class="table table3">
          <tr>
            <td>与上述条件关系：</td>
            <td>{{}}</td>
          </tr>
          <tr>
            <td>出现区域：</td>
            <td>{{}}</td>
          </tr>
          <tr>
            <td>出现时间：</td>
            <td{{}}</td>
          </tr>
          <tr>
            <td>出现次数：</td>
            <td>{{}}</td>
          </tr>
        </table>
      </div>
      <div class="dialog-right">
        <p class="dialog-operation"><span class="keyFigures">列为重点人</span><span class="export">导出</span></p>
        <table class="dialog-list">
          <caption class="list-caption">触发预警MAC</caption>
          <tbody v-for="(item,index) in dialogList">
          <tr  :class="{'isOdd':index%2 != 0}">
            <td @click="innerVisible = true">{{item.code}}</td>
            <td :title="item.target">{{item.time}}</td>
            <td>
              <el-checkbox v-model="item.checked"></el-checkbox>
            </td>
          </tr>
          </tbody>
        </table>
      </div>
    </el-dialog>
  </div>

</template>

<script type="text/javascript">
  import pageCtrl from '../../components/pagingctrl.vue'

  export default {
    name: 'page2',
    data() {
      return {
        inputContext:'',
        value:0,
        currentPage:2,
        total:20,
        dialogVisible: false,
        options:[{value:0,label:'全部'},{value:1,label:'发现目标'},{value:2,label:'布控中'},{value:3,label:'已结束'}],
        listData:[
          {
            name:'布控任务',
            target:'重点人',
            startTime:'2018/02/19',
            endTime:'2018/09/19',
            warning:'未触发',
            state:'布控中',
            checked:false
          },
          {
            name:'布控任务',
            target:'重点人',
            startTime:'2018/02/19',
            endTime:'2018/09/19',
            warning:'未触发',
            state:'布控中',
            checked:false
          },
          {
            name:'布控任务',
            target:'重点人',
            startTime:'2018/02/19',
            endTime:'2018/09/19',
            warning:'未触发',
            state:'布控中',
            checked:false
          },
          {
            name:'布控任务',
            target:'重点人',
            startTime:'2018/02/19',
            endTime:'2018/09/19',
            warning:'未触发',
            state:'布控中',
            checked:false
          },
        ],
        dialogList:[{code:'EC-56-A8-36-23-95',time:'2108-11-21 12:32',checked:false},{code:'EC-56-A8-36-23-95',time:'2108-11-21 12:32',checked:false},
          {code:'EC-56-A8-36-23-95',time:'2108-11-21 12:32',checked:false},{code:'EC-56-A8-36-23-95',time:'2108-11-21 12:32',checked:false}],
        outerVisible: false,
        innerVisible: false,
        activeName: 'second'
      };
    },
    methods:{
      changePage(val){
        console.log("当前点击页数",val)
      },
      handleClick(tab, event) {
        console.log(tab, event);
      }
    },
    components:{
      pageCtrl,
    }
  }
</script>
<style>
  .el-dialog__body{
    overflow: hidden;
  }
</style>
<style lang="scss" scoped>
  .page2-wrap{
    margin:20px auto;
    padding:0 20px;
    color:#000;
    .isOdd{
      background: #ccc;
    }
  }
  .page2-search{
    height:80px;
    line-height: 80px;
    padding:0 40px;
    background: #409EFF;
    .pg2-left{
      float:left;
    }
    .pg2-right{
      float:right;
    }
    .pg2-input{
      width:350px;
    }

  }
  .pg2-main{

    .pg2-edit{
      margin: 30px 10px;
      text-align: right;
      font-weight: 700;
      font-size:16px;
      span{
        cursor: pointer;
      }
      span:hover{
        color:#409EFF;
      }
    }
    table{
      width:100%;
      table-layout: fixed;
      text-align: center;
      border:1px solid #ccc;
      thead{
        background-color: #409EFF;
      }
      th{
        font-weight: 600;
        text-align: center;
      }
      tr,th{
        height:60px;
      }
      tbody{
        /*tr:hover{*/
        /*background: #F0F0F0;*/
        /*}*/
      }
    }
  }
  .dialog{
    .table{
      border-bottom: 1px solid #ccc;
      width:450px;
      tr{
        height:30px;
      }
      td:first-child{
        width:150px;
        text-align: right;
      }
    }
    .dialog-left{
      float:left;
      width:400px;
    }
    .dialog-right{
      float:right;
      .dialog-operation{
        text-align: right;
        font-weight: 700;
        padding:15px 0;
        span{
          cursor: pointer;
          font-size: 14px;
        }
        span:hover{
          color:#409EFF;
        }
        .export{
          padding:0 10px;
        }
      }
      .dialog-list{
        border:1px solid #ccc;
        width:400px;
        tr{
          height:30px;
          td:first-of-type{
            cursor: pointer;
          }
          td:first-of-type:hover{
            color:#409EFF;
          }
        }
        .list-caption{
          background: #409EFF;
          color:#ffff;
          height:40px;
          line-height: 40px;
          text-align: center;
          font-size: 16px;
        }
      }
    }
  }
</style>



