<template>

    <div class="page2-wrap">
      <!--查询开始-->
      <div class="page2-search">
       <section class="pg2-left">
         <el-input class="pg2-input" v-model="taskName" placeholder="请输入查询任务信息"></el-input>
         <el-select v-model="targetValue" placeholder="请选择">
           <el-option
             v-for="item in targetList"
             :key="item.status"
             :label="item.label"
             :value="item.status">
           </el-option>
         </el-select>
         <span class="showDetial" v-text="showDetial"></span>
       </section>
        <section class="pg2-right">
          <el-button type="success" @click="search">查询</el-button>
        </section>
      </div>
      <!--查询结束-->
      <div class="pg2-main">
        <!--页面左部分开始-->
      <div class="pg2-main-left">
        <el-button type="primary"class="but-center" @click="dialogAddTask=true">添加布控任务</el-button>
        <ul>
          <li class="showAllTask">查看全部任务</li>
          <li v-for="item in taskList"  v-if="item.status==0"><span class="task-state1">{{item.task_name}}</span><i class="iconfont icon-shezhi"></i></li>
          <li v-for="item in taskList" v-if="item.status==1" ><span  class="task-state2">{{item.task_name}}</span><i class="iconfont icon-shezhi"></i></li>
          <li v-for="item in taskList"  v-if="item.status==2"><span class="task-state3">{{item.task_name}}</span><i class="iconfont icon-shezhi"></i></li>
        </ul>
      </div>
        <!--页面左部分结束-->
        <!--页面右部分开始-->
        <div class="pg2-main-right">
          <em class="show-list"  @click="transitionShow = !transitionShow">列表</em>
            <transition name="slide-fade">
              <MacList v-if="transitionShow"  @hiddenList="hiddenList" @showTabList="showTabList"></MacList>
            </transition>
        </div>
      </div>
      <!--页面右部分结束-->
      <!--分页插件开始-->
      <pageCtrl :totalCount="total" :curr="currentPage" @changePage="changePage"></pageCtrl>
      <!--分页插件结束-->

      <!--点击右侧的列表中的Mac  dialog开始部分-->
      <TabList :outer="dialogTabList" @choseDialog="choseTab"></TabList>
      <!--点击布控任务设置  dialog结束部分-->
      <!--点击新增任务 dialog开始部分-->
      <AddTask :outer="dialogAddTask" @choseDialog="choseAdd"></AddTask>
      <!--点击新增任务 dialog结束部分-->
    </div>

</template>

<script type="text/javascript">
  import {Arrangement} from '../../api/setupHttp'
  import pageCtrl from '../../components/pagingctrl.vue'
  import TabList from '../components/tabList.vue'
  import MacList from '../components/list.vue'
  import AddTask from '../components/addTask.vue'

  export default {
     name: 'page2',
     data() {
      return {
        transitionShow:false,
        dialogAddTask:false,
        dialogTabList: false,
        showDetial:'XX任务中发现8个目标',
        taskName:'',
        targetValue:0,
        currentPage:1,
        total:20,
        dialogVisible: false,
        targetList:[{status:0,label:'全部'},{status:1,label:'未开始'},{status:3,label:'发现目标'},{status:2,label:'布控中'},{status:4,label:'已结束'}],
        listData:[
          {
            name:'布控任务',
            target:'重点人',
            startTime:'2018/02/19',
            endTime:'2018/09/19',
            warning:'未触发',
            state:'布控中',
            checked:false
          },
          {
            name:'布控任务',
            target:'重点人',
            startTime:'2018/02/19',
            endTime:'2018/09/19',
            warning:'未触发',
            state:'布控中',
            checked:false
          },
          {
            name:'布控任务',
            target:'重点人',
            startTime:'2018/02/19',
            endTime:'2018/09/19',
            warning:'未触发',
            state:'布控中',
            checked:false
          },
          {
            name:'布控任务',
            target:'重点人',
            startTime:'2018/02/19',
            endTime:'2018/09/19',
            warning:'未触发',
            state:'布控中',
            checked:false
          },
        ],
        taskList:[],

      };
    },
    methods:{
      changePage(val){
        console.log("当前点击页数",val)
      },
      showTabList(val){
        this.dialogTabList = true;
      },
      hiddenList(val){
        this.transitionShow=val;
      },
      choseTab(){
        this.dialogTabList=false;
      },
      choseAdd(){
        this.dialogAddTask=false;
      },
      search(){
        let params={
          taskName:this.taskName,
          status:this.targetValue,
          pageNum:this.currentPage,
          pageSize:10,
        };
        this.taskList=Arrangement.search(params).data;
      }
    },
    components:{
      pageCtrl,
      TabList,
      MacList,
      AddTask
    }
  }
</script>
<style>
  @import 'https://cdn.jsdelivr.net/npm/animate.css@3.5.1';
  .el-dialog__body{
    overflow: hidden;
  }
</style>
<style lang="scss" scoped>
  .page2-wrap{
    margin:0 auto;
    padding:20px;
    color:#000;
    background: #fff;
    .isOdd{
      background: #ccc;
    }
  }
  .page2-search{
    height:80px;
    line-height: 80px;
    padding:0 40px;
    background: #409EFF;
    .showDetial{
      color:#f73333;
    }
    .pg2-left{
      float:left;
    }
    .pg2-right{
      float:right;
    }
    .pg2-input{
      width:350px;
    }

  }
  .pg2-main{
    padding:20px 0;
    /*overflow: hidden;*/
    .pg2-main-left{
      float:left;
      width:300px;
      text-align: center;
      ul{
        margin-top:20px;
        border-left:1px solid #c0c4cc;
        border-right:1px solid #c0c4cc;
      }
      li{
        height:40px;
        line-height: 40px;
        cursor: pointer;
        text-align: center;
        border-top:1px solid #ccc;
        span{
          padding: 0 10px;
        }
      }
      li:last-child{
        border-bottom: 1px solid #ccc;
      }
      li:hover{
        opacity:0.5;
      }
      .task-state1{
        color:#992323;
      }
      .task-state2{
        color:#00ff00;
      }
      .task-state3{
        color:blue;
      }
    }
    .pg2-main-right{
      position: relative;
      height:400px;
      margin-left: 310px;
      border:1px solid #ccc;
      .show-list{
        color:#fff;
        text-align: center;
        position: absolute;
        display: inline-block;
        width: 20px;
        padding:10px 6px;
        right:10px;
        bottom:90px;
        background: #409EFF;
        border-radius: 2px;
      }
    }
  }
  /* 可以设置不同的进入和离开动画 */
  /* 设置持续时间和动画函数 */
  .slide-fade-enter-active {
    transition: all .4s cubic-bezier(1.0, 0.5, 0.8, 1.0);
  }
  .slide-fade-leave-active {
    transition: all .8s cubic-bezier(1.0, 0.5, 0.8, 1.0);
  }
  .slide-fade-enter, .slide-fade-leave-to {
    transform: translateX(200px);
    opacity: 0;
  }
</style>



